export const BlockArrowIcon = ({right, disabled, ...props}) => {
	const Wrapper = styled("div")`
		display: flex;
		opacity: ${disabled ? '0.3' : '1'};
		pointer-events: ${disabled ? 'none' : ''};
		position: relative;
		max-height: 40px;
		max-width: 40px;
		&:hover, :nth-of-type(3) {
			cursor: pointer;
			background-color: #fff;
		},
		&:hover > svg {
			left: -13px;
			transition: all 0.4s ease;
		}
		transform: rotate(${right ? '180' : '0'}deg);
	`;
	
	const ArrowLeftButton = styled("div")`
		width: 40px;
		height: 40px;
		border-top: solid 1px #202020;
		border-right: solid 1px #202020;
		border-bottom: solid 1px #202020;
	`;

	const LeftGap = styled("div")`
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		&:after {
			width: 1px;
			height: 15px;
			content: "";
			border-left: solid 1px #202020;
		}
		&:before {
			width: 1px;
			height: 15px;
			content: "";
			border-left: solid 1px #202020;
		}
	`;
	return (
		<Wrapper {...props}>
			<LeftGap />
			<ArrowLeftButton />
			<RightArrowIcon
				sx={{
				transform: "rotate(180deg)",
				position: "absolute",
				top: 8,
				right: 0,
				left: -8,
				bottom: 3,
				zIndex: 2,
				}}
			/>
		</Wrapper>
	)
}

