import { styled } from "@mui/system";


const BrandWrapper = styled('div')`
    width: 166px;
    height: 108px;
    background-image: url("images/brand_Badagoni.png");
    background-repeat: no-repeat;
    background-size:100% 100%;
`


function Brand() {
    return (
        <BrandWrapper></BrandWrapper>
    )
  }
  
  export default Brand;
  
  
  