import { MenuIcon } from '../../../spiritIcons';
import { Drawer, Typography, Grid } from '@mui/material/';
import * as React from 'react';
import { styled } from "@mui/system";
import { PhoneIcon, EmailIcon, LocationIcon, FacebookIcon, TelegramIcon, InstagramIcon } from '../../../spiritIcons'

const Hamburger = ({ white, ...props }) => {
    const [state, setState] = React.useState(false)

    const toggleDrawer = (toggle) => {
        console.log("hello");
        setState(toggle);
    };


    const CloseButton = styled('div')`
        cursor: pointer;
        font-size: 24px;
        margin-left: 368px;
    `

    const StyledLink = styled('div')`
        cursor: pointer;
        display: flex;
        justify-content: center;
        &:hover {
            &:before {
              display: inline-block;
              content: '';
              width: 23px;
              border-top: solid 1px #fbfbfb;
              margin-right: 15px;
              margin-top: 25px;
            }
            &:after {
              display: inline-block;
              content: '';
              width: 23px;
              border-top: solid 1px #fbfbfb;
              margin-left: 15px;
              margin-top: 25px;
            }
        }
    `

    return (
        <>
            <MenuIcon white={white ? true : false } onClick={() => toggleDrawer(true)} sx={{ cursor: 'pointer' }} {...props} />
            <Drawer
                sx = {{
                    '.MuiDrawer-paperAnchorRight': {
                        backgroundColor: '#0f0f0f',
                        padding: '50px 78px 67px 48px',
                        width: '614px',
                        color: '#fbfbfb',
                    }
                }}
                anchor='right'
                open={state}
                onClose={() => toggleDrawer(false)}
            >
                <Grid container>
                    <Grid container item>
                        <Typography variant="body2" fontSize="21px">EN</Typography>
                        <Typography variant="body2" fontSize="21px" sx={{
                            marginLeft: '15px',
                            color: '#9e9e9e'
                        }}>GEO</Typography>
                        <CloseButton onClick={() => toggleDrawer(false)}>X</CloseButton>
                    </Grid>
                    <Grid container item justifyContent="center">
                        <Typography variant="body1" fontSize="50px" sx={{
                            textAlign: 'center',
                            width: '170px',
                            borderBottom: '1px solid'
                        }}>SPIRIT.</Typography>
                    </Grid>
                    <Grid container item alignItems="center" direction="column" sx={{
                        marginTop: '64.5px',
                        gap: '45px'
                    }}>
                        <StyledLink variant="body1" fontSize="35px">Wine</StyledLink>
                        <StyledLink variant="body1" fontSize="35px">Chacha</StyledLink>
                        <StyledLink variant="body1" fontSize="35px">Accessories</StyledLink>
                        <StyledLink variant="body1" fontSize="35px">Exclusives</StyledLink>
                    </Grid>
                    <Grid container item sx={{gap: '36px', marginTop: '75px'}}>
                        <Grid item container ml="95px" sx={{gap: '33px'}}>
                            <PhoneIcon />
                            <Typography variant="body1" fontSize="20px">+995 555 555 555</Typography>
                        </Grid>
                        <Grid item container ml="95px" sx={{gap: '33px'}}>
                            <EmailIcon />
                            <Typography variant="body1" fontSize="20px">mail@gmail.com</Typography>
                        </Grid>
                        <Grid item container ml="95px" sx={{gap: '33px'}}>
                            <LocationIcon />
                            <Typography variant="body1" fontSize="20px">address</Typography>
                        </Grid>
                    </Grid>
                    <Grid container item  ml="95px" sx={{gap: '41px', marginTop: '63px'}}>
                        <TelegramIcon />
                        <FacebookIcon />
                        <LocationIcon />
                    </Grid>
                    <Grid item container justifyContent="Center" mt="25px">
                        <Typography variant="body1" fontSize="15px">2021 all rights reserved</Typography>
                    </Grid>
                </Grid>
            </Drawer>
        </>
    )
}

export default Hamburger;