import {useState} from 'react'
import { Typography, Grid } from "@mui/material";
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import SpiritInput from './SpiritInput';
import SpiritButton from './SpiritButton';



const SpiritSignUpModal = (props) => {
    const {handleModal, handleCloseModal} = props;

    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [email, setEmail] = useState('');
    const [repeatPassword, setRepeatPassword] = useState('');
    const [selected, setSelected] = useState('no');
    const [formErrors, setFormErrors] = useState({
        username: '',
        password: '',
        email: '',
        repeatPassword: '',
        ageRestrcted: '',
    })
    

    const resetForm = () => {
        setUsername('');
        setPassword('');
    }
    
    const validateInputs = () => {
        let valid = true;
        const changeFormState = (setState, newState, field) => {
            setState(prevState => {
                const stateObj = Object.assign({}, prevState)
                stateObj[field] = newState
                return stateObj;
            })
            return false;
        }
        if(username !== '') {
            if(username.length < 4 || username.length > 10) valid = changeFormState(setFormErrors, 'Username must be 4-10 characters long', 'username')
        } else {
            valid = changeFormState(setFormErrors, 'This field shouldn\'t be blank', 'username');
        }
        if(password !== '') {
            if(password.length < 4) valid = changeFormState(setFormErrors, 'Password must be at least 4 characters long', 'password');
        } else {
            valid = changeFormState(setFormErrors, 'This field shouldn\'t be blank', 'password');
        }

        if(email !== '') {
            if(!email.toLowerCase().match(/\S+@\S+/g)){
                valid = changeFormState(setFormErrors, 'Please, enter valid email (Ex: mail@gmail.com)', 'email');
            }
        } else {
            valid = changeFormState(setFormErrors, 'This field shouldn\'t be blank', 'email');
        }

        if(password !== repeatPassword) valid = changeFormState(setFormErrors, 'Passwords doesn\'t match', 'repeatPassword')

        if(selected !== 'yes') valid = changeFormState(setFormErrors, 'You should be at least 18 years old', 'ageRestricted')

        return valid
    }

    const handleSubmit = () => {
        setFormErrors({username: '', password: ''})
        const validation = validateInputs();
        if(validation){
            resetForm()
            console.log('SUCCESS', username, password, email)
        }
    }

    return (
      <Grid
        container
        sx={{ p: "39px 53px" }}
        justifyContent="center"
        flexDirection="column"
        gap={3}
      >
        <Grid item>
          <Typography variant="body1" fontSize="25px" textAlign="center">
            Registration
          </Typography>
        </Grid>
        <Grid item>
          <SpiritInput
            label="Username"
            value={username}
            error={formErrors.username}
            onChange={(e) => setUsername(e.target.value)}
          />
        </Grid>
        <Grid item>
          <SpiritInput
            label="Email"
            placeholder="EX: mail@gmail.com"
            value={email}
            error={formErrors.email}
            onChange={(e) => setEmail(e.target.value)}
          />
        </Grid>
        <Grid item>
          <SpiritInput
            label="Password"
            error={formErrors.password}
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </Grid>
        <Grid item>
          <SpiritInput
            label="Repeat Password"
            error={formErrors.repeatPassword}
            value={repeatPassword}
            onChange={(e) => setRepeatPassword(e.target.value)}
          />
        </Grid>
        <Grid item>
          <FormControl
            component="fieldset"
            focused={false}
            sx={{
              "& .MuiFormControlLabel-label": { fontSize: "20px" },
              "& .MuiFormLabel-root": { fontSize: "20px" },
            }}
          >
            <FormLabel component="legend" fontSize="20px">
              Are you +18?
            </FormLabel>
            {formErrors.ageRestricted && (
              <Typography fontSize="16px" color="red">
                {formErrors.ageRestricted}
              </Typography>
            )}
            <RadioGroup
              aria-label="age-restriction"
              value={selected}
              name="radio-buttons-group"
              onChange={(e) => setSelected(e.target.value)}
            >
              <FormControlLabel
                value="yes"
                control={<Radio disableRipple color="secondary" />}
                label="Yes"
              />
              <FormControlLabel
                value="no"
                control={<Radio disableRipple color="secondary" />}
                label="No"
              />
            </RadioGroup>
          </FormControl>
        </Grid>
        <Grid item>
          <SpiritButton sx={{ width: "100%" }} onClick={() => handleSubmit()}>
            Sign Up
          </SpiritButton>
        </Grid>
        <Grid item container justifyContent="center" gap={1}>
          <Typography variant="body1" component="div" fontSize="16px">
            Already have an account?
          </Typography>
          <Typography variant="body1" component="div" fontSize="16px" sx={{cursor: 'pointer'}} onClick={() => handleModal('Login')}>
            <strong>Log In</strong>
          </Typography>
        </Grid>
      </Grid>
    );
}

export default SpiritSignUpModal;