import { useState } from "react";
import { Typography, Grid, Popover} from "@mui/material";
import { styled } from '@mui/material/styles';
import { SortIcon, FilterIcon, ExpandMoreIcon } from '../../spiritIcons';




const Sort = (props) => {

    const {handleSort} = props

    const [anchorEl, setAnchorEl] = useState(null);
    const [sort, setSort] = useState("");

    const handleClick = (event) => {
      setAnchorEl(event.currentTarget);
    };
  
    const handleClose = () => {
      setAnchorEl(null);
    };
  
    const open = Boolean(anchorEl);
    const id = open ? 'simple-popover' : undefined;

    const SortWrapper = styled(Grid)`
        cursor: pointer;
    `
    const PopoverItem = styled(Typography)`
        cursor: pointer;
        padding: 4px;
        &:hover {
            transform: translateY(-3%);
            transition: transform .5s ease;
        }
    `

    const  handlePopoverItemClick = (event) => {
        setSort(event.currentTarget.textContent)
        handleSort(event.currentTarget.textContent)
        setAnchorEl(null)
    }

    return (
      <>
        <div onClick={handleClick}>
            <SortWrapper item container gap={1}>
                <SortIcon />
                <Typography fontSize="20px">Sort by {sort}</Typography>
                <ExpandMoreIcon key={Math.random()} style={{transform: open ? 'rotate(180deg)' : 'rotate(0deg)', transition: 'transform 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms'}}/>
            </SortWrapper>
        </div>
        <Popover
          id={id}
          open={open}
          anchorEl={anchorEl}
          onClose={handleClose}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'right',
          }}
          transformOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
          elevation={2}
        >
          <PopoverItem variant="body1" fontSize="20px" onClick={handlePopoverItemClick}>Price: High to Low</PopoverItem>
          <PopoverItem variant="body1" fontSize="20px" onClick={handlePopoverItemClick}>Price: Low to High</PopoverItem>
          <PopoverItem variant="body1" fontSize="20px" onClick={handlePopoverItemClick}>Z-A</PopoverItem>
          <PopoverItem variant="body1" fontSize="20px" onClick={handlePopoverItemClick}>A-Z</PopoverItem>
        </Popover>
      </>
    );
}

export default Sort;