import { Typography, ButtonBase } from "@mui/material";
import { styled } from "@mui/system";

function SpiritButton({ children, white, mobile, sx, ...props }) {
  const color = white ? "#202020" : "#fbfbfb";
  const bgColor = white ? "#fff" : "#202020";
  const width = mobile ? "225px" : "300px";
  const height = mobile ? "57px" : "63px";
  return (
    <ButtonBase
      sx={{
        width: width,
        height: height,
        backgroundColor: `${bgColor}`,
        color: color,
        fontSize: "20px",
        "&:hover": {
          "&:before": {
            display: "inline-block",
            content: '""',
            width: "23px",
            borderTop: `solid 1px ${color}`,
            marginRight: "15px",
          },
          "&:after": {
            display: "inline-block",
            content: '""',
            width: "23px",
            borderTop: `solid 1px ${color}`,
            marginLeft: "15px",
          },
        },
        ...sx,
      }}
      disableRipple
      {...props}
    >
      <Typography variant="body3" component="div">
        {children}
      </Typography>
    </ButtonBase>
  );
}

export default SpiritButton;
