import { useState, useEffect } from 'react';
import { Typography, Grid } from "@mui/material";
import { styled } from "@mui/system";
import Footer from "../footer/Footer";
import Navbar from "../navbar/Navbar";
import useMediaQuery from '@mui/material/useMediaQuery';
import AddressAdd from '../Profile/Address/AddressAdd';
import CartCheckoutList from '../CartCheckoutList/CartCheckoutList';
import CartListItem from '../CartItemList/CartListItem';
import {getCookie} from '../../spiritUtils';

const ContentWrapper = styled('div')`
    max-width: 1770px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 75px;
`

const Wrapper = styled('div')`
    background-color: #f5f5f5;
`
const FooterWrapper = styled('div')`
  width: 100%;
  background-color: #202020;
  height: 100%;
  margin-top: 205px;
  color: #fbfbfb;
`



const CheckoutPage = () => {
    const [cartItems, setCartItems] = useState(null)

    useEffect(() => {
        const isLoggedIn = sessionStorage.getItem('logged_in');
        const csrftoken = getCookie('csrftoken');
        if(isLoggedIn){
            fetch(`${process.env.REACT_APP_BACKEND_DOMAIN}cart`, {
              method: "GET",
              headers: {
                "Content-Type": "application/json;charset=utf-8",
                Authorization: `JWT ${localStorage.getItem("token")}`,
                "X-CSRFToken": csrftoken,
              },
            })
              .then((res) => {
                  if(res.ok){
                      return res.json()
                  } else {
                      return false;
                  }
                })
              .then((cartData) => {
                if(!cartData) return;
                fetch(
                  `${process.env.REACT_APP_BACKEND_DOMAIN}/wineproduct/?wine=%27test%27`
                )
                  .then((res) => {
                    if(res.ok){
                        return res.json()
                    } else {
                        return false;
                    }
                  })
                  .then((productData) => {
                    if(!productData) return;
                    let cart = [];
                    productData.menu.forEach((productItem) => {
                      cartData.items.forEach((cartItem) => {
                        if (productItem.id === cartItem.product_id) {
                          cart.push({
                            id: productItem.id,
                            image: productItem.image,
                            name: productItem.name,
                            sales: productItem.sales,
                            quantity: cartItem.quantity,
                            inStockQuantity: productItem.quantity,
                            price: productItem.price,
                            type: productItem.type,
                            vendor: productItem.vendor,
                            color: productItem.color
                          });
                        }
                      });
                    });
                    setCartItems(cart);
                  });
              });
        }

        if(!isLoggedIn){
            setCartItems(JSON.parse(localStorage.getItem('cartItems')))
        }
    }, [])

    const matches = useMediaQuery('(max-width:900px)');

    return (
      <Wrapper>
        <Navbar fixed />
        <ContentWrapper>
          <Grid item container>
            {!matches && (
              <>
                <Grid item container xs={4}>
                  <Grid item container>
                   <AddressAdd />
                  </Grid>
                  <Grid item container>
                  </Grid>
                </Grid>
                <Grid item xs={8}>
                    <Grid item container sx={{maxHeight: '500px', overflowY: 'auto', gap: '15px'}}>
                        {cartItems &&
                        cartItems.length > 0 &&
                        cartItems.map((item) => (
                        <CartListItem
                            key={item.id}
                            data={item}
                        />))
                        }
                    </Grid>
                    <CartCheckoutList data={cartItems} />
                </Grid>
              </>
            )}
            {matches && (
                <>  
                    mobile
                </>
            )}
          </Grid>
        </ContentWrapper>
        <FooterWrapper>
            <div style={{padding: '0 15px 0 15px'}}>
                <Footer />
            </div>
        </FooterWrapper>
      </Wrapper>
    );
}

export default CheckoutPage;