import { styled } from "@mui/system";
import { Box, Typography, Grid, Hidden} from "@mui/material";
import SpiritButton from "../SpiritButton"


const BigPhoto = styled('div')`
    width: 470px;
    height: 646px;
    display: inline-block;
    background-image: url("images/big-photo-2.jpg");
    background-repeat: no-repeat;
    background-size:100% 100%;
`
const SmallPhoto = styled('div')`
    width: 333px;
    height: 444px;
    background-image: url("images/small-photo-1.jpg");
    margin: 287px 0px 0px 211px;
    background-repeat: no-repeat;
    background-size:100% 100%;
    border: solid 10px #f2f2f2;
`

const PhotoWrapper = styled('div')`
    margin: 0 auto;
`

const HeightWrapper = styled('div')`
`

const CommonBanner = () => {
    return (
        <HeightWrapper>
            <Hidden mdDown>
                <Grid container>
                    <Grid item container xs={7} >
                        <PhotoWrapper>
                            <BigPhoto>
                                <SmallPhoto />
                            </BigPhoto>
                        </PhotoWrapper>
                    </Grid>
                    <Grid item container xs={5} flexDirection="column">
                        <Typography variant="h2" mt="138px">Modern Wine.</Typography>
                        <Typography variant="h3" mt="30px">Best Wine from Georgia</Typography>
                        <Typography variant="body2" mt="68px" sx={{
                            display: "flex",
                            maxWidth: "560px",
                            lineHeight: "35px",
                            '&:before': {
                                display: "block",
                                content: '""',
                                borderTop: 'solid 2px #0f0f0f',
                                width: '100%',
                                marginTop: '17px',
                                marginRight: '25px'
                            }
                        }}>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam purus quam. Lorem ipsum dolor sit ame.
                        Aliquam purus quam. Lorem ipsum dolor sit ame.
                        </Typography>
                        <Grid item container mt="60px">
                            <SpiritButton>View Catalog</SpiritButton>
                        </Grid>
                    </Grid>
                </Grid>
            </Hidden>
            <Hidden mdUp>
                <Grid container >
                    <Grid item container flexDirection="column" alignItems="center">
                        <Typography variant="h2" fontSize="40px" mt="138px">Modern Wine.</Typography>
                        <Typography variant="h3" fontSize="25px" mt="16px">Best Wine from Georgia</Typography>
                        <Typography variant="body2" fontSize="16px" mt="52px" sx={{
                            display: "flex",
                            maxWidth: "560px",
                            lineHeight: "35px",
                            '&:before': {
                                display: "block",
                                content: '""',
                                borderTop: 'solid 2px #0f0f0f',
                                width: '34px',
                                marginTop: '17px',
                                marginRight: '25px'
                            },
                            '&:after': {
                                display: "block",
                                content: '""',
                                borderTop: 'solid 2px #0f0f0f',
                                width: '34px',
                                marginTop: '17px',
                                marginRight: '25px'
                            }
                        }}>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam purus quam. Lorem ipsum dolor sit ame.
                        Aliquam purus quam. Lorem ipsum dolor sit ame.
                        </Typography>
                    </Grid>
                    <Grid item container mt="30px">
                        <PhotoWrapper>
                            <BigPhoto>
                                <SmallPhoto />
                            </BigPhoto>
                        </PhotoWrapper>
                    </Grid>
                    <Grid item container mt="135px" justifyContent="center">
                        <SpiritButton mobile>View Catalog</SpiritButton>
                    </Grid>
                </Grid>
            </Hidden>
        </HeightWrapper>
    )
}

export default CommonBanner;