import {useState} from 'react';
import { UserIcon } from '../../../spiritIcons';
import { styled } from "@mui/system";
import { useHistory } from "react-router-dom";
import SpiritModals from "../../SpiritModals";


const UserIconStyled = styled(UserIcon)`
    &:hover {
        cursor: pointer;
    }
`

const User = () => {
    const history = useHistory();

    const [open, setOpen] = useState(false);

    const handleOpen = () => {
        if(!sessionStorage.getItem('logged_in')){
            setOpen(true)
        } else {
            history.push('/profile')
        }
    }
    
    const handleClose = () => setOpen(false);

    return (
        <>
            <UserIconStyled onClick={handleOpen} />
            <SpiritModals open={open} onClose={handleClose} handleCloseModal={handleClose}/>
        </>
    )
}

export default User;