import { useState, useEffect } from 'react';
import { CartIcon } from '../../../spiritIcons';
import { styled } from "@mui/system";
import { Popover, Grid, Typography } from "@mui/material/";
import { useHistory } from "react-router-dom";
import SpiritButton from '../../SpiritButton';
import {dbt, getCookie, calculateSubtotal, calculateItemTotal} from '../../../spiritUtils';

const CartIconStyled = styled(CartIcon)`
    &:hover {
        cursor: pointer;
    }
`

const CartIconWrapper = styled('div')`
    width: 24px;
    height: 24px;
    position: relative;
`
const Counter = styled('div')`
  width: 23px;
  height: 23px;
  border-radius: 100%;
  background-color: #fbfbfb;
  position: absolute;
  font-size: 20px;
  color: #202020;
  line-height: 19px;
  text-align: center;
  z-index: 2;
  left: 14px;
  top: -7px;
`


const getItemQuantity = (cart) => {
  let quantity = 0;
  if(!cart) return quantity;
  cart.forEach(item => {
    quantity += parseInt(item.quantity)
  })
  return quantity;
}


const Cart = (props) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const [cart, setCart] = useState(null);
  const [total, setTotal] = useState(0);
  const [error, setError] = useState(null);
  const history = useHistory();

  const {data} = props;

  const handleCartClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleCartClose = () => {
    setAnchorEl(null);
  };


  useEffect(() => {
    const isLoggedIn = sessionStorage.getItem('logged_in');
    const csrftoken = getCookie('csrftoken');

    if(isLoggedIn && !data) {
      fetch(`${process.env.REACT_APP_BACKEND_DOMAIN}cart`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json;charset=utf-8",
          Authorization: `JWT ${localStorage.getItem("token")}`,
          "X-CSRFToken": csrftoken,
        },
      })
        .then((res) => {
            if(res.ok){
                return res.json()
            } else {
                setError('Something went wrong')
                return false;
            }
          })
        .then((cartData) => {
          if(!cartData) return;
          fetch(
            `${process.env.REACT_APP_BACKEND_DOMAIN}/wineproduct/?wine=%27test%27`
          )
            .then((res) => {
              if(res.ok){
                  return res.json()
              } else {
                  setError('Something went wrong')
                  return false;
              }
            })
            .then((productData) => {
              if(!productData) return;
              let cart = [];
              productData.menu.forEach((productItem) => {
                cartData.items.forEach((cartItem) => {
                  if (productItem.id === cartItem.product_id) {
                    cart.push({
                      id: productItem.id,
                      image: productItem.image,
                      name: productItem.name,
                      sales: productItem.sales,
                      quantity: cartItem.quantity,
                      inStockQuantity: productItem.quantity,
                      price: productItem.price,
                      type: productItem.type,
                      vendor: productItem.vendor,
                      color: productItem.color
                    });
                  }
                });
              });
              setCart(cart);
              setTotal(calculateSubtotal(cart))
            });
        });
    }

    if(!isLoggedIn && !data) {
      setCart(JSON.parse(localStorage.getItem('cartItems')));
      setTotal(calculateSubtotal(data))
    }

    if(data) {
      setCart(data);
      setTotal(calculateSubtotal(data))
    }

  
  }, [data])


  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  const GridWrapper = styled(Grid)`
    max-width: 418px;
    min-width: 200px;
    padding: 20px
  `;

  const WineImage = styled('img')`
    max-height: 126px;
    max-width: 50px;
  `


  return (
    <>
      <CartIconWrapper onClick={(e) => handleCartClick(e)}>
        <Counter>{getItemQuantity(cart)}</Counter>
        <CartIconStyled sx={{position: 'absolute'}} />
      </CartIconWrapper>
      <Popover
        open={open}
        onClose={handleCartClose}
        anchorEl={anchorEl}
        elevation={1}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "right",
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "right",
        }}
      >
        <GridWrapper container>
          <Grid
            container
            item
            spacing={2}
            sx={{ maxHeight: "400px", overflowY: "auto" }}
          >
            {cart ? (
              cart.map((item) => (
                <Grid item container direction="row" key={item.id}>
                  <Grid item container xs={3} justifyContent="center">
                    <WineImage src={process.env.REACT_APP_BACKEND_DOMAIN + 'images/' + item.image} />
                  </Grid>
                  <Grid item container xs={9} direction="column">
                    <Grid item>
                      <Typography variant="body1" fontSize="20px">
                        {dbt(item.name)}
                      </Typography>
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" fontSize="13px">
                        Price: {item.sales ? item.sales : item.price}
                      </Typography>
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" fontSize="13px">
                        Quantity: {item.quantity}
                      </Typography>
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" fontSize="21px">
                        Subtotal: {calculateItemTotal(item)}$
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
              ))
            ) : (
              <Grid container item>
                <Grid item>
                  <Typography
                    variant="body1"
                    fontSize="24px"
                    textAlign="center"
                  >
                    Your cart is empty, please add products
                  </Typography>
                </Grid>
              </Grid>
            )}
          </Grid>
          <Grid item container justifyContent="flex-end" mt="50px">
            <Typography variant="body1" fontSize="25px">
              Subtotal: {total}$
            </Typography>
          </Grid>
          <Grid item container justifyContent="center">
            <SpiritButton sx={{ mt: "20px" }} onClick={() => history.push('/cart')}>Go to Cart</SpiritButton>
          </Grid>
        </GridWrapper>
      </Popover>
    </>
  );
}

export default Cart;