import React, {useState, useEffect} from 'react';
import { IncreaseIcon, DecreaseIcon, CrossIcon, CloseIcon } from '../../spiritIcons';
import {Typography, Grid} from '@mui/material';
import { styled } from "@mui/system";
import {dbt, getCookie} from '../../spiritUtils';
import useMediaQuery from '@mui/material/useMediaQuery';

const WineImage = styled('img')`
    max-height: 201px;
    max-width: 62px;
  `

const WineImageMobile = styled('img')`
    max-height: 131px;
    max-width: 40px;
`

const styledLink = styled(Typography)`

`


const CartListItem = (props) => {
    const [count, setCount] = useState(props.data?.quantity || 1);


    const {data, handleDelete, handleUpdate} = props
    const {image, name, price, sales, quantity, vendor, type, color, inStockQuantity, id} = data


    const isLoggedIn = sessionStorage.getItem('logged_in');
    const csrftoken = getCookie('csrftoken')
    const matches = useMediaQuery('(min-width:900px)');



    const handleIncreaseButtonClick = () => {
        if(isLoggedIn){
            setCount(count + 1);
            if(count >= inStockQuantity) {
                setCount(inStockQuantity)
              } else {
                fetch("https://spirit.ge:8000/cart/", {
                  method: "POST",
                  headers: {
                    "Content-Type": "appliction/json;charset=utf-8",
                    Authorization: `JWT ${localStorage.getItem("token")}`,
                    "X-CSRFToken": csrftoken,
                  },
                  body: JSON.stringify({
                    action: "add",
                    productId: id
                  }),
                  credentials: "include",
                })
                .then(res => {
                    if(res.ok){
                        handleUpdate(id, count + 1);
                    }
                    return res.json()
                })
              }
        }

        if(!isLoggedIn) {
            if (count >= inStockQuantity) {
              setCount(inStockQuantity);
            } else {
              handleUpdate(id, count + 1, true);
              setCount(count + 1);
            }
        }
    }

    const handleDecreaseeButtonClick = () => {
        if(isLoggedIn){
            if(count !== 1){
                fetch("https://spirit.ge:8000/cart/", {
                  method: "POST",
                  headers: {
                    "Content-Type": "appliction/json;charset=utf-8",
                    Authorization: `JWT ${localStorage.getItem("token")}`,
                    "X-CSRFToken": csrftoken,
                  },
                  body: JSON.stringify({
                    action: "remove",
                    productId: id,
                  }),
                  credentials: "include",
                })
                .then(res => {
                    if(res.ok){
                        handleUpdate(id, count - 1)
                        setCount(count - 1)
                    }
                });
            }
        }


        if(!isLoggedIn){
            if(count !== 1){
                handleUpdate(id, count - 1, true)
                setCount(count - 1)
            }
        }

    }

    const handleDeleteButtonClick = () => {
        if(isLoggedIn){
            fetch("https://spirit.ge:8000/cart/", {
              method: "POST",
              headers: {
                "Content-Type": "appliction/json;charset=utf-8",
                Authorization: `JWT ${localStorage.getItem("token")}`,
                "X-CSRFToken": csrftoken,
              },
              body: JSON.stringify({
                action: "removeAll",
                productId: id,
              }),
              credentials: "include",
            });
            handleDelete(id);
        }

        if(!isLoggedIn){
            handleDelete(id, true)
        }
    }



    return (
      <Grid container>
        <Grid container item xs={5}>
          <Grid item container xs={4} md={3} justifyContent="center">
            {!matches ? (
              <WineImageMobile
                src={process.env.REACT_APP_BACKEND_DOMAIN + "/images/" + image}
              />
            ) : (
              <WineImage
                src={process.env.REACT_APP_BACKEND_DOMAIN + "/images/" + image}
              />
            )}
          </Grid>
          <Grid
            item
            container
            xs={8}
            md={9}
            direction="column"
            justifyContent="center"
          >
            <Grid item>
              <Typography variant="body1" fontSize={!matches ? "14px" : "25px"}>
                {<b>{dbt(name)}</b>}
              </Typography>
            </Grid>
            <Grid item>
              <Typography variant="body1" fontSize={!matches ? "12px" : "16px"}>
                {dbt(color)} {dbt(type)}
              </Typography>
            </Grid>
            <Grid item>
              <Typography variant="body1" fontSize={!matches ? "12px" : "16px"}>
                Winery: {dbt(vendor)}
              </Typography>
            </Grid>
            <Grid item>
              <Typography
                component="a"
                variant="body1"
                fontSize={!matches ? "12px" : "16px"}
              >
                View in detail
              </Typography>
            </Grid>
          </Grid>
        </Grid>
        <Grid container item xs={6}>
          <Grid
            container
            item
            xs={12}
            md={4}
            justifyContent="center"
            alignItems="center"
          >
            <Typography component="div" fontSize={!matches ? "16px" : "25px"}>
              <b>{sales ? sales : price}$</b>
            </Typography>
          </Grid>
          <Grid
            container
            item
            xs={12}
            md={8}
            justifyContent="center"
            alignItems="center"
          >
            <Grid item container justifyContent="center">
              <IncreaseIcon onClick={handleIncreaseButtonClick} />
              <Typography
                component="div"
                fontSize={!matches ? "16px" : "25px"}
                pl="1em"
                pr="1em"
                sx={{
                    userSelect: 'none'
                }}
              >
                {count}
              </Typography>
              <DecreaseIcon onClick={handleDecreaseeButtonClick} />
            </Grid>
          </Grid>
        </Grid>
        <Grid container item xs={1} justifyContent="center" alignItems="center">
          {!matches ? <CloseIcon /> : <CrossIcon onClick={handleDeleteButtonClick} />}
        </Grid>
      </Grid>
    );
};

export default CartListItem;