import { styled } from "@mui/system";
import { Typography } from "@mui/material";

    const StyledInput = styled('textarea')`
        width: 100%;
        min-height: 100px;
        min-width: 100%;
        max-width: 100%;
        padding: 10px;
        font-size: 20px;
        font-family: Raleway;
        &:focus {
            outline: none;
        }
    `

    const InputWrapper = styled('div')`
        min-width: 100%;
    `




const SpiritTextArea = ({label, placeholder, error, type="text", ...props}) => {

    return (
      <InputWrapper>
        <Typography variant="body1" fontSize="20px" component="div">
          <b>{label}</b>
        </Typography>
        <StyledInput
          type={type}
          style={{border: error ? 'solid 1px red' : 'solid 1px #202020'}}
          placeholder={placeholder}
          spellCheck={false}
          error
          {...props}
        />
        {error && (
          <Typography variant="body1" fontSize="16px" color="red" component="div">
            {error}
          </Typography>
        )}
      </InputWrapper>
    );
}

export default SpiritTextArea;