import Navbar from '../navbar/Navbar';
import { useState,useEffect } from 'react'
import { Typography, Hidden, Grid, Drawer } from "@mui/material";
import { styled } from "@mui/system";
import WineFilterSidebar from "../WineFilter/WineFilterSidebar";
import ProductDisplay from "../WineFilter/ProductDisplay";
import Sort from "../WineFilter/Sort";
import { FilterIcon } from '../../spiritIcons'
import Footer from "../footer/Footer";

const ContentWrapper = styled('div')`
    max-width: 1770px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 75px;
    // background-color: #f5f5f5;
`

const MobileDrawerButton = styled('div')`
  display: flex;
  cursor: pointer;
  gap: 5px;
`

const FooterWrapper = styled('div')`
  width: 100%;
  background-color: #202020;
  height: 100%;
  margin-top: 100px;
  color: #fbfbfb;
`


const ProductPage = () => {
    const [filter, setFilter] = useState({})
    const [sort, setSort] = useState("")
    const [drawerOpen, setDrawerOpen] = useState(false)

    const handleFilters = (obj) => {
        setFilter({...obj.current});
    }

    const handleSort = (value) => {
        setSort(value);
    }

    const toggleDrawer = (toggle) => {
        setDrawerOpen(toggle);
    };
    


    return (
      <>
        <Navbar fixed />
        <div  style={{backgroundColor: '#f5f5f5'}}>
          <ContentWrapper>
            <Grid container>
              <Grid item container>
                1
              </Grid>
              <Hidden mdDown>
                <Grid item container justifyContent="space-between">
                  <Grid item>
                    <Typography variant="h2" fontSize="50px" color="#0c0c0c">
                      Online shop.
                    </Typography>
                  </Grid>
                  <Grid item>
                    <Sort handleSort={(sort) => handleSort(sort)} />
                  </Grid>
                </Grid>
              </Hidden>
              <Hidden mdUp>
                <Grid item container justifyContent="center">
                  <Typography variant="h2" fontSize="35px" color="#0c0c0c">
                    Online shop.
                  </Typography>
                </Grid>
              </Hidden>
              <Grid item container mt="30px">
                <Grid item container md={2} xs={12}>
                  <Hidden mdDown>
                    <WineFilterSidebar
                      filterSettings={(filters) => handleFilters(filters)}
                    />
                  </Hidden>
                  <Hidden mdUp>
                    <Grid item container justifyContent="space-between" pb="15px">
                      <Grid item>
                        <MobileDrawerButton onClick={() => toggleDrawer(true)}>
                          <FilterIcon height="39px" width="39px" htmlSize="39px" />
                          <Typography variant="body1" fontSize="16px">Filter</Typography>
                        </MobileDrawerButton>
                        <Drawer
                          sx={{
                            ".MuiDrawer-paperAnchorLeft": {
                              backgroundColor: "#f5f5f5",
                              padding: "50px 40px 47px 28px",
                              maxWidth: "428px",
                            },
                          }}
                          anchor="left"
                          open={drawerOpen}
                          onClose={() => toggleDrawer(false)}
                        >
                          <Grid item container direction="column" alignItems="flex-end"> 
                              <Grid item sx={{cursor: 'pointer'}} onClick={() => toggleDrawer(false)}>
                                  X
                              </Grid>
                              <Grid item>
                                  <WineFilterSidebar
                                      mobile
                                      filterSettings={(filters) => handleFilters(filters)}
                                  />
                              </Grid>
                          </Grid>
                        </Drawer>
                      </Grid>
                      <Grid item>
                        <Sort handleSort={(sort) => handleSort(sort)} />
                      </Grid>
                    </Grid>
                  </Hidden>
                </Grid>
                <Grid item container md={10} xs={12}>
                  <ProductDisplay filters={filter} sort={sort} />
                </Grid>
              </Grid>
            </Grid>
          </ContentWrapper>
          <FooterWrapper>
            <ContentWrapper>
              <Footer />
            </ContentWrapper>
          </FooterWrapper>
        </div>
      </>
    );
}

export default ProductPage;