import { useState, useEffect } from "react";
import { Typography, Hidden, Grid, Box } from "@mui/material";
import { styled } from "@mui/system";

const TypeSelector = (props) => {
  const { types, handleFilters, selected } = props;
  const [active, setActive] = useState(selected);




  const StyledSelector = styled(Box)`
    width: 100%;
    height: 60px;
    background-color: #f2f2f2;
    color: #202020;
    padding-top: 10px;
    padding-left: 15px;
    &:hover {
        border-left: solid 3px #202020;
        cursor: pointer;
    }
  `;

  const handleClick = (event) => {
    setActive(event.currentTarget.textContent);
    handleFilters(event.currentTarget.textContent)
  };

  return (
    <>
      {types.map((item) => (
        <StyledSelector
          component="div"
          onClick={handleClick}
          key={item}
          sx={
            item === active
              ? {
                  backgroundColor: "#202020",
                  color: "#fbfbfb",
                }
              : {}
          }
        >
          <Typography variant="body1" fontSize="25px">
            {item}
          </Typography>
        </StyledSelector>
      ))}
    </>
  );
};

export default TypeSelector;
