import React, {useState, useEffect, memo} from 'react';
import { Typography, Grid } from "@mui/material";
import SpiritButton from '../SpiritButton';
import {dbt, calculateSubtotal, calculateItemTotal} from '../../spiritUtils';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useHistory } from 'react-router-dom';





const CartCheckoutList = (props) => {
    const [subtotal, setSubtotal] = useState(0)
    const {data, checkout} = props;
    const isLoggedIn = sessionStorage.getItem('logged_in')
    const history = useHistory();
    

    useEffect(() => {
        if(Array.isArray(data)) setSubtotal(calculateSubtotal(data))
    }, [data])


    const matches = useMediaQuery('(min-width:900px)');

    const handleClick = () => {
        history.push('/checkout')
    }

    
    return (
        <Grid item container flexDirection="column">
            <Grid item container>
                <Grid item xs={6}>
                    <Typography component="div"  variant="body1" fontSize={matches ? '25px' : '20px'}><b>Subtotal:</b></Typography>
                </Grid>
                <Grid item xs={6}>
                    <Typography component="div"  variant="body1" fontSize={matches ? '25px' : '20px'} textAlign="end"><b>${subtotal}</b></Typography>
                </Grid>
                <Grid item container xs={12} p="10px 0 10px 10px">
                    {data && data.map(item => (
                        <Grid item container xs={12} key={item.id}>
                            <Grid item xs={6}>
                                <Typography component="div" fontSize={matches ? '20px' : '16px'} variant="body1">{dbt(item.name)} x{item.quantity}</Typography>
                            </Grid>
                            <Grid item xs={6}>
                                <Typography component="div" fontSize={matches ? '20px' : '16px'} variant="body1" textAlign="end">${calculateItemTotal(item)}</Typography>
                            </Grid>
                        </Grid>
                    ))}
                </Grid>
            </Grid>
            <Grid item container>
                <Grid item xs={6}>
                    <Typography component="div"  variant="body1" fontSize={matches ? '25px' : '20px'}><b>Delivery:</b></Typography>
                </Grid>
                <Grid item xs={6}>
                    <Typography component="div"  variant="body1" fontSize={matches ? '25px' : '20px'} textAlign="end"><b>0$</b></Typography>
                </Grid>
            </Grid>
            <Grid item container p="20px 0 20px 0">
                <Grid item xs={6}>
                    <Typography component="div"  variant="body1" fontSize={matches ? '25px' : '20px'}><b>Total:</b></Typography>
                </Grid>
                <Grid item xs={6}>
                    <Typography component="div"  variant="body1" fontSize={matches ? '25px' : '20px'} textAlign="end"><b>${subtotal}</b></Typography>
                </Grid>
            </Grid>
            <Grid item alignSelf="center" sx={{display: checkout ? 'none' : 'flex'}}>
                <SpiritButton mobile disabled={!isLoggedIn} onClick={handleClick}>Go to checkout</SpiritButton>
            </Grid>
            {
            !isLoggedIn 
            &&
            <Grid item alignSelf="center" pt="10px">
                <Typography component="div"  variant="body1" fontSize="20px"><b>Note:</b> You must authorize to purchase</Typography>
            </Grid>
            }
        </Grid>
    );
};

export default CartCheckoutList;