import { styled } from "@mui/system";
import { Typography } from "@mui/material";
import {getCookie} from '../../../spiritUtils';
import {TrashIcon} from "../../../spiritIcons";

    const StyledAddress = styled('div')`
        width: 100%;
        max-width: 400px;
        min-height: 50px;
        padding: 0;
        padding-left: 20px;
        padding-right: 20px;
        font-size: 20px;
        font-family: Raleway;
        border: solid 1px #202020;
        display: flex;
        &:focus {
            outline: none;
        }
        align-items: center;
        gap: 10px;
    `

    const AddressText = styled('div')`
        flex-grow: 1;
    `




const AddressInput = ({data, handleUpdate}) => {

    const {address, phone, id} = data;

    const handleClick = () => {
        fetch("https://spirit.ge:8000/address/", {
          method: "POST",
          headers: {
            "Content-Type": "application/json;charset=utf-8",
            Authorization: `JWT ${localStorage.getItem("token")}`,
            "X-CSRFToken": getCookie('csrftoken'),
          },
          body: JSON.stringify({
            action: "delete",
            info: { id: id },
          }),
        })
        .then(res => res.json())
        .then(json => handleUpdate(id));
        handleUpdate(id)
    }

    return (
        <StyledAddress>
            <AddressText>{address}, {phone}</AddressText>
            <TrashIcon sx={{cursor: 'pointer'}} onClick={handleClick}/>
        </StyledAddress>
    );
}

export default AddressInput;