import Navbar from '../navbar/Navbar';
import Banner from '../banner/Banner';
import { styled } from "@mui/system";
import SpiritWineItem from '../carousel/SpiritWineItem';
import Carousel from '../carousel/Carousel';
import CommonBanner from '../banner/CommonBanner';
import Blog from "../blog/Blog";
import Brands from "../brands/Brands";
import Footer from "../footer/Footer";

const PageWrapper = styled('div')`
  max-width: 1770px;
  margin: 0 auto;
`
const BannerWrapper = styled('div')`
  background-image: url("images/banner.jpg");
  background-repeat: no-repeat;
  height: 981px;
  background-size:100% 100%;
  width: 100%;
  display: inline-block;
`

const CommonBannerWrapper = styled('div')`
  background-image: url("images/abstract-lines-transparent-png.png");
  background-repeat: no-repeat;
  height: 981px;
  background-size:100% 100%;
  width: 100%;
  display: inline-block;
  margin-top: 233px;
`

const FooterWrapper = styled('div')`
  width: 100%;
  background-color: #202020;
  height: 100%;
  margin-top: 205px;
  color: #fbfbfb;
`

function MainPage() {
    return (
      <>
        <Navbar white />
        <Navbar />
        <BannerWrapper>
          <PageWrapper>
            <Banner />
          </PageWrapper>
        </BannerWrapper>
        <PageWrapper>
          <Carousel />
        </PageWrapper>
        <CommonBannerWrapper>
          <PageWrapper>
            <CommonBanner />
          </PageWrapper>
        </CommonBannerWrapper>
        <PageWrapper>
          <Blog />
        </PageWrapper>
        <PageWrapper>
          <Brands />
        </PageWrapper>
        <FooterWrapper>
          <PageWrapper>
            <Footer />
          </PageWrapper>
        </FooterWrapper>
      </>
    );
  }
  
  export default MainPage;
  
  
  