import { useState, forwardRef } from 'react';
import { Typography, Grid } from "@mui/material";
import { styled } from "@mui/system";
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import { ExpandMoreIcon } from "../../spiritIcons";
import Slider from '@mui/material/Slider';
import InputUnstyled from '@mui/core/InputUnstyled';


const StyledInput = styled('input')`
    border: solid 1px rgba(32, 32, 32, 0.4);
    width: 100%;
    height: 100%;
    text-align: center;
    font-size: 19px;
    font-family: Raleway;
    &:toggle {
        border: none;
    }
`
const CustomInput = forwardRef(function CustomInput(props, ref) {
    return (
      <InputUnstyled components={{ Input: StyledInput }} {...props} ref={ref} />
    );
  });

// const CustomInput = (props) => {
//   return (
//     <InputUnstyled components={{Input: StyledInput}} {...props} />
//   );
// } 


const RangeAccordion = (props) => {

  const {name, min, max, handleFilters} = props
  const [range, setRange] = useState([min, max])

  const handleChange = (event, newValue) => {
    setRange(newValue);
    // handleFilters(range);
  };

  const handleLeftInputChange = (event) => {
    let value = event.target.value === false || isNaN(event.target.value) ? 0 : parseInt(event.target.value);
    if(value < min) value = min;
    if(value > max) value = max;
    setRange(prev => [value, prev[1]])
  }


  const handleRightInputChange = (event) => {
    let value = event.target.value === false || isNaN(event.target.value) ? 0 : parseInt(event.target.value);
    if(value < min) value = min;
    if(value > max) value = max;
    setRange(prev => [prev[0], value])
  }


  const handleBlur = () => {
    handleFilters(range);
  };


    return (
      <Accordion disableGutters square elevation={0} sx={{width: '100%'}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography variant="body2" fontSize="22px">{name}</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Slider
            value={range}
            onChange={handleChange}
            onMouseUp={handleBlur}
            aria-labelledby="input-slider"
            color="secondary"
            min={min}
            max={max}
          />
          <Grid container>
            <Grid container item xs={6}>
                <CustomInput
                    value={range[0]}
                    onChange={handleLeftInputChange}
                    onBlur={handleBlur}
                />
            </Grid>
            <Grid container item xs={6}>
                <CustomInput
                    value={range[1]}
                    onChange={handleRightInputChange}
                    onBlur={handleBlur}
                />
            </Grid>
          </Grid>
        </AccordionDetails>
      </Accordion>
    )
}

export default RangeAccordion;