import { cloneElement } from 'react';
import { Box, AppBar, Toolbar, Typography, Grid, Slide} from "@mui/material";
import Cart from './menuItems/Cart'
import Hamburger from './menuItems/Hamburger'
import Search from './menuItems/Search'
import User from './menuItems/User'
import Hidden from '@mui/material/Hidden';
import { styled } from "@mui/system";
import ExpandableSearchBar from "./ExpandableSearchBar";
import useScrollTrigger from '@mui/material/useScrollTrigger';
import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useHistory } from "react-router-dom";





const Navbar = ({ white, fixed, data }) => {
  const theme = useTheme();
  const breakpoint = useMediaQuery(theme.breakpoints.up('md'));
  const history = useHistory();

  function handleLogoClick(){
    console.log('hello')
    history.push("/")
  }

  const StyledLink = styled(Typography)`
    cursor: pointer;
  `


  const variant = white ? {
    bgColor: "#fff",
    txtColor: "spiritBlack.main",
    position: "static",
    hamburger: true,
    display: 'none'
  } : {
    bgColor: "#202020",
    txtColor: "#fbfbfb",
    position: "fixed",
    hamburger: false
  }

  const AppBarWrapper = styled('div')`
    height: 75px;
    width: 100%;
    background-color: ${variant.bgColor};
  `;

  function HideOnScroll(props) {
    const { children } = props;
  
    const trigger = useScrollTrigger({
      threshold: 1050,
      disableHysteresis: true
    });
    
    if(!white){
      return (
        <Slide
          direction="down"
          appear={fixed ? false : true}
          in={fixed ? true : trigger && !white}
          sx={{
            backgroundColor:variant.bgColor
          }}
        >
          {children}
        </Slide>
      );
    }

    return (
      <>
        <AppBarWrapper>
          {children}
        </AppBarWrapper>
      </>
    )
  }


  return (
    <HideOnScroll>
      {/* <AppBarWrapper> */}
        <AppBar
          elevation={0}
          sx={{
            height: "75px",
            maxWidth: "1511px",
            backgroundColor: variant.bgColor,
            position: variant.position,
            color: variant.txtColor,
            margin: '0 auto',
          }}
        >
          <Toolbar disableGutters sx={{ width: '100%', pl: "25px", pr: "25px" }}>
            <Grid container wrap="nowrap" sx={{ width: '100%'}}>
              <Hidden mdUp>
                <Grid item xs={2} container alignContent="center">
                  <Hamburger white={variant.hamburger} />
                </Grid>
                <Grid item xs={8}>
                  <StyledLink variant="body1" fontSize="30px" textAlign="center" onClick={() => handleLogoClick()}>
                    SPIRIT.
                  </StyledLink>
                </Grid>
                <Grid item xs={2} container alignContent="center" justifyContent="flex-end">
                  <Cart />
                </Grid>
              </Hidden>
              <Hidden mdDown>
                <Grid item xs={2}>
                  <StyledLink variant="body1" fontSize="50px" onClick={() => handleLogoClick()}>
                    SPIRIT.
                  </StyledLink>
                </Grid>
                <Grid
                  item
                  container
                  md={7}
                  lg={8}
                  gap={5}
                  sx={{ alignContent: "center", justifyContent: "flex-end" }}
                >
                  <StyledLink variant="body2" onClick={() => history.push('/')}>Home</StyledLink>
                  <StyledLink variant="body2" onClick={() => history.push("/shop")}>Shop</StyledLink>
                  <StyledLink variant="body2" onClick={() => console.log("blog")}>
                    Blog
                  </StyledLink>
                </Grid>
                <Grid
                  item
                  md={3}
                  lg={2}
                  container
                  alignContent="center"
                  justifyContent="flex-end"
                  gap={3}
                > 
                  <Search />
                  <Cart data={data} />
                  <User />
                  <Hamburger white={variant.hamburger} />
                </Grid>
            </Hidden>
            </Grid>
          </Toolbar>
        </AppBar>
      {/* </AppBarWrapper> */}
    </HideOnScroll>
  );
};

export default Navbar;
