import { styled } from "@mui/system";
import { Typography, Grid, Hidden} from "@mui/material";
import { PhoneIcon, EmailIcon, LocationIcon, TelegramIcon, FacebookIcon, InstagramIcon, VisaIcon} from '../../spiritIcons';
import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery'


const Footer = () => {
    const theme = useTheme();
    const matches = useMediaQuery(theme.breakpoints.up('md'));

    const Container = styled('div')`
        display: flex;
        flex-direction: column;
    `

    const FirstRow = styled('div')`
        display: flex;
        flex-direction: ${matches ? 'row' : 'column'};
        justify-content: space-between;
        ${matches ? '' : 'align-items: center;'}
        border-bottom: 1px solid #fbfbfb;
        margin-top: 96px;
    `
    const Column = styled('div')`
        display: flex;
        flex-direction: column;
        margin-bottom: 80px;
    `
    const Info = styled('div')`
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin-top: 40px;
    `

    const SpecialInfo = styled('div')`
        display: flex;
        align-items: center;
        justify-content: flex-start;
    `

    const Socials = styled('div')`
        display: flex;
        margin-top: 35px;
    `

    const SecondRow = styled('div')`
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 35px;
    `


    return (
        <Container>
            <FirstRow>
                <Column>
                    <Typography variant="body1" fontSize="60px">SPIRIT.</Typography>
                </Column>
                <Column>
                    <Typography variant="body1" fontSize="20px">Catalog</Typography>
                    <Typography variant="body1" fontSize="20px" mt="42px">Acessories</Typography>
                    <Typography variant="body1" fontSize="20px" mt="42px">Blog</Typography>
                    <Typography variant="body1" fontSize="20px" mt="42px">Contact</Typography>
                </Column>
                <Column>
                    <SpecialInfo>
                        <PhoneIcon htmlColor="#fbfbfb" />
                        <Typography variant="body1" fontSize="20px" ml="25px" >+995 555 555 555</Typography>
                    </SpecialInfo>
                    <Info>
                        <EmailIcon htmlColor="#fbfbfb"/>
                        <Typography variant="body1" fontSize="20px" ml="25px">somemail@gmail.com</Typography>
                    </Info>
                    <Info>
                        <LocationIcon htmlColor="#fbfbfb"/>
                        <Typography variant="body1" fontSize="20px" ml="25px">Misamarti</Typography>
                    </Info>
                </Column>
                <Column>
                    <Typography variant="body1" fontSize="20px">Socials</Typography>
                    <Socials>
                        <TelegramIcon sx={{ marginRight: '40px'}} />
                        <FacebookIcon sx={{ marginRight: '40px'}} />
                        <InstagramIcon />
                    </Socials>
                </Column>
            </FirstRow>
            <SecondRow>
                <Typography variant="body1" fontSize="15px" mr="55px">2021 all rights reserved</Typography>
                <VisaIcon />
            </SecondRow>
        </Container>
    )
}

export default Footer;