import { useEffect, useState } from 'react'
import { Typography, Hidden, Grid, Box } from "@mui/material";
import { styled } from "@mui/system";
import SpiritWineItem from '../carousel/SpiritWineItem';
import dummyData from "../../api/ui/dummyData";
import {dbt} from '../../spiritUtils';
import Pagination from "./Pagination";
import Footer from '../footer/Footer';


const ProductDisplay = (props) => {
    const [page, setPage] = useState(1);
    const [wineItems, setWineItems] = useState([])

    useEffect(() => {
        fetch(`${process.env.REACT_APP_BACKEND_DOMAIN}/wineproduct/?wine=%27test%27`).then(item => item.json()).then(item => setWineItems(item.menu))
      }, [])

    const {filters, sort} = props
    console.log(filters, sort);
    const ProductWrapper = styled('div')`
        display: flex;
        gap: 20px;
        width: 100%;
        flex-wrap: wrap;
        justify-content: center;
    `
    const handleFilters = (data, filters) => {
        let result = [...data];
        for(let key in filters){
            const filter = filters[key];
            let min, max;
            if(key === "price" || key === "alcohol" || key === "year"){
              max = Math.max(...filter);
              min = Math.min(...filter);
            }
            switch(key){
                case "view":
                    result = result.filter(item => {
                        if(!item.type) return false;
                        if(dbt(item.type, false).toLowerCase().includes(filter.toLowerCase())) return true;
                        return false;
                    });
                    break;
                case "price":
                    result = result.filter(item => {
                        if(!item.price) {
                            return false;
                        }
                        return min <= parseFloat(item.price) && max >= parseFloat(item.price)
                    });
                    break;
                case "alcohol":
                    result = result.filter(item => {
                        if(!item.alcoholPercent) return false;
                        if(!(min <= parseFloat(item.alcoholPercent) && max >= parseFloat(item.alcoholPercent))) console.log(item.alcoholPercent, `min-${min}, max-${max} alcohol`);
                        return min <= parseFloat(item.alcoholPercent) && max >= parseFloat(item.alcoholPercent)
                    });
                    break;
                case "year":
                    result = result.filter(item => {
                        if(!item.year) return false;
                        if(!(min <= parseInt(item.year) && max >= parseInt(item.year))) console.log(item.year, `min-${min}, max-${max}`);
                        return min <= parseInt(item.year) && max >= parseInt(item.year)
                    });
                    break;
                case "color":
                    result = result.filter(item => filter.some(color => {
                        if(!item.color) return false;
                        if(color === 'Other'){
                            return (
                              dbt(item.color.toLowerCase(), false) !== "rose" &&
                              dbt(item.color.toLowerCase(), false) !== "white" &&
                              dbt(item.color.toLowerCase(), false) !== "red"
                            );
                        } else {
                            return dbt(item.color.toLowerCase(), false).includes(color.toLowerCase());
                        }
                    }))
                    break;
                case "region":
                    result = result.filter(item => filter.some(region => {
                        if(item.region) return false; 
                        return region.toLowerCase() === item.region.toLowerCase() 
                    }))
                    break;
                case "sweetness":
                    result = result.filter(item => filter.some(sweetness => {
                        if(!item.type) return false; 
                        if(!(item.type.toLowerCase().includes(sweetness.toLowerCase()))) console.log(item.type, sweetness.toLowerCase())
                        return item.type.toLowerCase().includes(sweetness.toLowerCase()) 
                    }))
                    break;
                default:
                    break;
            }
        }
        return result;
    }

    const handleSort = (data, sort) => {
        let result = [...data];

        switch(sort){
            case "Price: High to Low":
                result = result.sort((a,b) => {
                    const val1 = parseFloat(a.price);
                    const val2 = parseFloat(b.price);
                    if(val1 < val2){
                        return 1
                    }
                    if(val1 > val2){
                        return -1
                    }
                    return 0
                })
                break;
            case "Price: Low to High":
                result = result.sort((a,b) => {
                    const val1 = parseFloat(a.price);
                    const val2 = parseFloat(b.price);
                    if(val1 > val2){
                        return 1
                    }
                    if(val1 < val2){
                        return -1
                    }
                    return 0
                })
                break;
            case "Z-A":
                result = result.sort((a,b) => a.name < b.name)
                break;
            case "A-Z":
                result = result.sort((a,b) => a.name > b.name)
                break;
            default:
                break;
        }
        return result
    }

    

    function pagination(data, itemsPerPage) {
        const array = [...data]
        const pages = Math.ceil(array.length/itemsPerPage)
        const result = [];
        for(let i = 0; i < pages; i++){
            result.push(array.splice(0, itemsPerPage))
        }
        return result
    }

    let pages = pagination(handleFilters(handleSort(wineItems, sort), filters), 12)

    const handlePageChange = (event, value) => {
        setPage(value);
    }

    return (
        <Grid item container>
            <Grid item xs={12}>
                <ProductWrapper>
                    {pages && pages[page-1]?.map(item => <SpiritWineItem wine={item} mobile key={item.id} />)}
                    {pages.length === 0 && <Typography variant="body1" fontSize="24px" textAlign="center">Sorry, we couldn't find any matches.</Typography>}
                </ProductWrapper>
            </Grid>
            <Grid item container justifyContent="center">
                {pages.length > 0 && <Pagination count={pages.length} page={page} onChange={handlePageChange}/>}
            </Grid>
        </Grid>
    )
}

export default ProductDisplay;