import {useState} from 'react'
import { styled } from "@mui/system";
import { Typography, Grid, FormControlLabel, Checkbox } from "@mui/material";
import SpiritInput from './SpiritInput';
import SpiritButton from './SpiritButton';


const SpiritLogInModal = (props) => {
    const {handleModal, handleCloseModal} = props;


    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [checked, setChecked] = useState(false);
    const [formErrors, setFormErrors] = useState({
        username: '',
        password: '',
        checkbox: ''
    })
    

    const resetForm = () => {
      setUsername("");
      setPassword("");
    };

    const changeFormState = (setState, newState, field) => {
      setState(prevState => {
          const stateObj = Object.assign({}, prevState)
          stateObj[field] = newState
          return stateObj;
      })
    }
    
    const validateInputs = () => {
        let valid = true;
      
        if(username === '') {
            changeFormState(setFormErrors, 'This field shouldn\'t be blank', 'username');
            valid = false;
        }
        if(password === '') {
            changeFormState(setFormErrors, 'This field shouldn\'t be blank', 'password');
            valid = false;
        }

        return valid
    }

    const handleSubmit = () => {
        setFormErrors({username: '', password: ''})
        const validation = validateInputs();
        if(validation){
            resetForm()
            fetch(`${process.env.REACT_APP_BACKEND_DOMAIN}token-auth/`, {
              method: 'POST',
              headers: { 'Content-Type': 'application/json' },
              body: JSON.stringify({username: username, password: password})
            })
            .then(res => res.json())
            .then(json => {
              if(json.non_field_errors) changeFormState(setFormErrors, json.non_field_errors[0], 'backend');
              if(json.token){
                localStorage.setItem('token', json.token);
                sessionStorage.setItem('logged_in', true);
                sessionStorage.setItem('username', json.user.username);
              }
              handleCloseModal()
              window.location.reload()
            })
        }
    }

    return (
          <Grid
            container
            sx={{ p: "39px 53px" }}
            justifyContent="center"
            flexDirection="column"
            gap={3}
          >
            <Grid item>
              <Typography variant="body1" fontSize="25px" textAlign="center">
                Signing In
              </Typography>
              {formErrors.backend && <Typography varinat="body1" fontSize="20px" textAlign="center" color="red">{formErrors.backend}</Typography>}
            </Grid>
            <Grid item>
              <SpiritInput
                label="Username/Email"
                placeholder="EX: mail@gmail.com"
                value={username}
                error={formErrors.username}
                onChange={(e) => setUsername(e.target.value)}
              />
            </Grid>
            <Grid item>
              <SpiritInput
                label="Password"
                error={formErrors.password}
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </Grid>
            <Grid item>
              <FormControlLabel
                sx={{ "& .MuiFormControlLabel-label": { fontSize: "20px" } }}
                control={
                  <Checkbox
                    defaultChecked={checked}
                    disableRipple
                    sx={{ color: "#0f0f0f" }}
                    size="medium"
                    color="secondary"
                    onChange={() => setChecked(true)}
                  />
                }
                label="Remember me"
              />
            </Grid>
            <Grid item>
              <Typography
                variant="body1"
                component="div"
                fontSize="16px"
                color="#707070"
              >
                Forgot Your Password?
              </Typography>
              <SpiritButton
                sx={{ width: "100%", mt: "5px" }}
                onClick={() => handleSubmit()}
              >
                Log In
              </SpiritButton>
            </Grid>
            <Grid item container justifyContent="center" gap={1}>
              <Typography variant="body1" component="div" fontSize="16px">
                Don't have an account?
              </Typography>
              <Typography variant="body1" component="div" fontSize="16px" sx={{cursor: 'pointer'}} onClick={() => handleModal('Signup')}>
                <strong>Sign Up</strong>
              </Typography>
            </Grid>
          </Grid>
    );
}

export default SpiritLogInModal;