export const dbt = (objProp, useFun=true) => {
    if(objProp === null || objProp === undefined) return null;
    let hasLanguage = objProp.includes(';');
    if(useFun === false && hasLanguage) return objProp.split(';')[0]
    if(useFun === false && !hasLanguage) return objProp
    if(localStorage.getItem('language') === 'ge'){
        if(hasLanguage) return objProp.split(';')[1]
        return objProp
    } else {
        if(hasLanguage) return objProp.split(';')[0]
        return objProp
    }
}

export const getCookie = (name) => {
    if(!document.cookie) return null;

    const xsrfCookies = document.cookie.split(';').map(c => c.trim()).filter(c => c.startsWith(name + '='));

    if(xsrfCookies.length === 0) return null;

    return decodeURIComponent(xsrfCookies[0].split('=')[1])
}

export const calculateSubtotal = (products) => {
    let total = 0;
    if(!products) return total;
    if(!Array.isArray(products)) return total;
    products?.forEach(item => {
        if(item.sales) {
            total += parseFloat(item.sales) * parseFloat(item.quantity)
        } else {
            total += parseFloat(item.price) * parseFloat(item.quantity)
        }
    })
    return total.toFixed(2)
}

export const calculateItemTotal = (product) => {
    let total = 0;
    if(!product) return total;
    if(product.sales){
        total += parseFloat(product.sales) * parseFloat(product.quantity)
    } else {
        total += parseFloat(product.price) * parseFloat(product.quantity)
    }
    return total.toFixed(2);
}