import * as React from 'react';
import usePagination from '@mui/material/usePagination';
import { styled } from '@mui/material/styles';
import { BlockArrowIcon } from '../../spiritIcons';

const List = styled('ul')({
  listStyle: 'none',
  margin: 0,
  padding: '150px 0 0 0',
  display: 'flex',
  gap: '10px'
});

const StyledButton = styled('div')`
    width: 43px;
    height: 43px;
    font-size: 25px;
    text-align: center;
    &:hover {
        cursor: pointer;
        border: 1px solid #202020;
        border-radius: 100%;
    }
`

const StyledDots = styled('span')`
    fontSize: 25px;
    text-align: center;
`


export default function Pagination(props) {
  const { items } = usePagination({
    ...props
  });

  return (
    <nav>
      <List>
        {items.map(({ page, type, selected, ...item }, index) => {
          let children = null;

          if (type === 'start-ellipsis' || type === 'end-ellipsis') {
            children = <StyledDots></StyledDots>;
          } else if (type === 'page') {
            children = (
              <StyledButton
                type="button"
                style={{
                  color: selected ? '#fbfbfb' : '#202020',
                  borderRadius: selected ? '100%' : undefined,
                  backgroundColor: selected ? '#202020' : undefined,
                }}
                {...item}
              >
                {page}
              </StyledButton>
            );
          } else {
            if(type === 'previous'){
                children = (
                  <BlockArrowIcon {...item}/>
                );
            } else {
                children = (
                  <BlockArrowIcon right {...item}/>
                )
            }
          }

          return <li key={index}>{children}</li>;
        })}
      </List>
    </nav>
  );
}