import React, {useState, useEffect} from 'react';
import AddressInput from './AddressInput';
import { Typography, Grid } from "@mui/material";
import { styled } from "@mui/system";
import SpiritButton from '../../SpiritButton';
import {getCookie} from '../../../spiritUtils';


const AddressView = props => {
    const {handleComponentChange} = props;

    const [addresses, setAddress] = useState(null);

    const [update, setUpdate] = useState(null);


    useEffect(() => {
        const csrftoken = getCookie('csrftoken');
        fetch(`${process.env.REACT_APP_BACKEND_DOMAIN}/address`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json;charset=utf-8',
                Authorization: `JWT ${localStorage.getItem('token')}`,
                'X-CSRFToken':  csrftoken,
            },
        })
        .then(res => res.json())
        .then(json => {
            setAddress(json.items)
        })
    }, [update])

    const handleClick = () => {
        handleComponentChange('add');
    }

    return (
        <Grid item container>
            <Grid item>
                <Typography variant="body1" component="div" fontSize="20px"><b>Full address, Phone number</b></Typography>
            </Grid>
            <Grid item container justifyContent="center" gap="22px" mt="15px" sx={{maxHeight: '314px', overflowX: 'hidden'}}>
                {!addresses && <Typography variant="body1" component="div" fontSize="20px">No addresses to display, please add them.</Typography>}
                {addresses && addresses.map(item => <AddressInput key={item.id} data={item} handleUpdate={(id) => setUpdate(id)}/>)}
            </Grid>
            <Grid item container justifyContent="center" pt="48px">
                <SpiritButton onClick={handleClick}>Add New Address</SpiritButton>
            </Grid>
        </Grid>
    );
};



export default AddressView;