import { Typography, Hidden, Grid } from "@mui/material";
import { styled } from "@mui/system";
import { RightArrowIcon } from "../../spiritIcons";
import Brand from "./Brand";

const BrandWrapper = styled('div')`

`

const Wrapper = styled('div')`
    display: flex;
    position: relative;
`

const WrapperRight = styled('div')`
    display: flex;
    position: relative;
    transform:rotate(180deg);
`

const ArrowLeftButton = styled('div')`
    width: 40px;
    height: 40px;
    border-top: solid 1px #202020;
    border-right: solid 1px #202020;
    border-bottom: solid 1px #202020;
    &:hover {
        cursor: pointer;
        background-color: #fff;
        left: -5px;
    }
`

const LeftGap = styled('div')`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &:after {
        width: 1px;
        height: 15px;
        content: "";
        border-left: solid 1px #202020;
    }
    &:before {
        width: 1px;
        height: 15px;
        content: "";
        border-left: solid 1px #202020;
    }
`



function Brands() {
    return (
        <BrandWrapper>
            <Grid container>
                <Grid item container justifyContent="center">
                    <Typography align="center" mt="200px" variant="h3">Our Brands.</Typography>
                </Grid>
                <Grid item container justifyContent="space-evenly" wrap="wrap" mt="90px">
                    <Brand/>
                    <Brand/>
                    <Brand/>
                    <Brand/>
                    <Brand/>
                </Grid>
            </Grid>
        </BrandWrapper>
    )
  }
  
  export default Brands;
  
  
  